import { Button, Card, Link } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import CheckoutStep from "./shared/CheckoutStep";
import { createOrder , sendEmail } from "../actions/orderAction";
import Alert from "@material-ui/lab/Alert";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import { savePaymentMethod, saveShippingAddress } from "../actions/cartAction";
import validator from "validator";
import CircularProgress from "@mui/material/CircularProgress";
import SecurityIcon from '@mui/icons-material/Security';
import {payOrder } from "../actions/orderAction";
import { ORDER_PAY_RESET } from "../../constants/orderConstant";
import axios from "axios";
import  mongoose from "mongoose";




const PlaceOrder = ({ history, match }) => {
  const dispatch = useDispatch();
  const [sdkReady, setSdkReady] = useState(false);
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  const orderCreate = useSelector((state) => state.orderCreate);
  const { order, success, error , loading:orderCreateLoading } = orderCreate;
  const cart = useSelector((state) => state.cart);
  const { shippingAddress } = cart;
  const { cartItems } = cart;

  const orderId = new mongoose.Types.ObjectId();
  console.log("orderID",orderId);
  
  const orderPay = useSelector((state) => state.orderPay);
  const { loading: loadingPay, success: successpay } = orderPay;
  
  const [showCheckout , setShowCheckout] = useState(true);
  const [checkoutLoader, setCheckoutLoader] = useState(true);
  const [paymentMethod, setPaymentMethod] = useState("Credit card or Netbanking");
  const [showAddressEdit , setShowAddressEdit] = useState(true)
  const [address, setAddress] = useState(shippingAddress.address);
  const [city, setCity] = useState(shippingAddress.city);
  const [country, setCountry] = useState(shippingAddress.country);
  const [postalcode, setPostalcode] = useState(shippingAddress.postalcode);
  const [fullName, setFullName] = useState(shippingAddress.fullName);
  const [shippingMobile, setShippingMobile] = useState(
    shippingAddress.shippingMobile
  );
  const [message, setMessage] = useState("");




  //fun for decimal
  const addDecimal = (num) => {
    return (Math.round(num * 100) / 100).toFixed(2);
  };

  cart.itemPrice = addDecimal(
    cart.cartItems.reduce((acc, item) => acc + item.price * item.qty, 0)
  );

  cart.shippingPrice = addDecimal(cart.cartItems > 500 ? 0 : 50);
  cart.totalPrice = Number(cart.itemPrice) + Number(cart.shippingPrice);

  let checkoutLodaer = true;

  

  async function placeOrderHandler(){
    
    if (!validator.matches(fullName, /^[a-zA-Z ]{2,40}$/)) {
      alert("Invalid Name");
    } else if (!validator.matches(city, "^[a-zA-Z]+$")) {
      alert("Invalid City");
    } else if (!validator.isPostalCode(postalcode, "IN")) {
      alert("Invalid Postalcode");
    } else if (!validator.matches(address, `[A-Za-z0-9'\.\-\s\,]`)) {
      alert("Don't use specialize symbols in Address like - &(%#$^)");
    } else if (!validator.isMobilePhone(shippingMobile, ["en-IN"])) {
      alert("Invalid Mobile Number");
    } else {
       dispatch(savePaymentMethod(paymentMethod));
      setCheckoutLoader(false);
      
        dispatch(
          createOrder({
            orderId:orderId,
            orderItems: cart.cartItems,
            shippingAddress: cart.shippingAddress,
            paymentMethod: paymentMethod,
            itemPrice: cart.itemPrice,
            shippingPrice: cart.shippingPrice,
            totalPrice: cart.totalPrice,
            userEmail: userInfo.email,
          })
        );
      
    }
  };
  useEffect(() => {
    if (success) {
      history.push(`/shipping/placeorder/order/${order._id}`);
    }
    const script = document.createElement("script");
    script.src = `https://checkout.razorpay.com/v1/checkout.js`;
    script.onload = () => {
      setSdkReady(true);
    };
    script.async = true;
    document.body.appendChild(script);

    if (!order || successpay) {
      // dispatch(getOrderDetails(orderId));
      dispatch({ type: ORDER_PAY_RESET });
    }
    //    eslint-disable-next-line
  }, [history, success]);

  async function displayRazorpay() {
    setShowCheckout(true);
    if (!validator.matches(fullName, /^[a-zA-Z ]{2,40}$/)) {
      alert("Invalid Name");
    } else if (!validator.matches(city, "^[a-zA-Z]+$")) {
      alert("Invalid City");
    } else if (!validator.isPostalCode(postalcode, "IN")) {
      alert("Invalid Postalcode");
    } else if (!validator.matches(address, `[A-Za-z0-9'\.\-\s\,]`)) {
      alert("Don't use specialize symbols in Address like - &(%#$^)");
    } else if (!validator.isMobilePhone(shippingMobile, ["en-IN"])) {
      alert("Invalid Mobile Number");
    }else{
       await dispatch(savePaymentMethod(paymentMethod));
      const result = await axios.post("/payment/orders", {
        amount: cart.totalPrice.toString() * 100,
      });
      const { id } = result.data;
      console.log("response from order api", result.data);
  
      const options = {
        key: "rzp_test_9d7xWt1UVvVKRc", // Enter the Key ID generated from the Dashboard
        amount: cart.totalPrice.toString() * 100,
        currency: "INR",
        name: "Apneehatti Pvt.Ltd.",
        description: "Test Transaction",
        image: "",
        order_id: id,
  
        handler: async function (paymentResults) {
    
      if(paymentResults.razorpay_order_id) {
        setCheckoutLoader(false);
        console.log("payment result ", paymentResults);
        placeOrderHandler();
        console.log("payOrderID", orderId);
        dispatch(payOrder(orderId, paymentResults))
      } 
          
          
        },
  
        prefill: {
          name: fullName,
          email: userInfo.email,
          contact: shippingMobile,
        },
        notes: {
          address: address,
        },
      };
  
      const paymentObject = new window.Razorpay(options);
      paymentObject.open();
  
      paymentObject.on("payment.failed", function (response) {
        console.log(response.error.code);
        console.log(response.error.description);
        console.log(response.error.source);
        console.log(response.error.step);
        console.log(response.error.reason);
        console.log(response.error.metadata.order_id);
        console.log(response.error.metadata.payment_id);
      });
    }

   
  }

  async function saveAddressDetails(){
    await dispatch(
      saveShippingAddress({
        address,
        city,
        postalcode,
        country,
        fullName,
        shippingMobile,
      })
    )
    
    if(dispatch(saveShippingAddress)){
      alert("Shipping Detail saved successfully")
      setShowAddressEdit(true)
    }


  }



  return (
    <>
      <div className="checkoutGrid">
        <div className="checkoutGrid__left">
        <CheckoutStep fullWidth step1 step2 />
       {!checkoutLoader ? <div className="AlertBox"><CircularProgress  color="secondary" className="checkoutLoader" /></div>: null}
          <div className="checkoutContainer">
            <h3 className="checkoutGrid__serialNumb">1</h3>
            <div className="checkoutLogin">
              <h3 className="checkout__heading__title">LOGIN</h3>
              <h4>
                {fullName} <span>+91{shippingMobile}</span>
              </h4>
            </div>
          </div>
         {
           showAddressEdit ?      <div className="checkoutDeliveryGrid ">
           <h3 className="checkoutGrid__serialNumb">2</h3>
    
           <div className="checkoutLogin">
             <h3 className="checkout__heading__title">DELIVERY</h3>
             <h4>
               {fullName}
               <span>
                &nbsp; {address} , India , {postalcode}
               </span>
             </h4>
           </div>
           <Button onClick= {() => setShowAddressEdit(false)}>Edit</Button>
         </div> 
         :  <div className="checkoutGrid__div">
            <div className="checkoutGrid__div__heading">
              <h3 className="checkoutGrid__div__serialNumb">2</h3>
              <h3 className="checkout__div__heading__title">DELIVERY</h3>
            </div>
            <div className="checkout__address">
              <div className="checkoutForm__grid">
                <input
                  className="checkoutpage__input"
                  type="email"
                  placeholder="email"
                  defaultValue={userInfo.email}
                />
                <input
                  className="checkoutpage__input "
                  type="text"
                  value={fullName}
                  onChange={(e) => setFullName(e.target.value)}
                  placeholder="Full Name"
                  required
                />

                <input
                  className="checkoutpage__input"
                  variant="outlined"
                  id="outlined-basic"
                  size="small"
                  id="country"
                  type="text"
                  defaultValue="India"
                  disabled
                />

                <input
                  className="checkoutpage__input"
                  maxLength="10"
                  type="text"
                  value={shippingMobile}
                  onChange={(e) => setShippingMobile(e.target.value)}
                  placeholder="Mobile Number"
                  required
                />
              </div>
              <textarea
                className="checkoutpage__input checkoutpage__input__address"
                type="text"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
                placeholder="Address"
                required
              />

              <div className="checkoutForm__grid">
                <input
                  className="checkoutpage__input"
                  type="text"
                  value={city}
                  onChange={(e) => setCity(e.target.value)}
                  placeholder="City"
                  required
                />

                <input
                  className="checkoutpage__input"
                  maxLength="6"
                  type="text"
                  value={postalcode}
                  onChange={(e) => setPostalcode(e.target.value)}
                  placeholder="Pincode"
                  required
                />

                <Button onClick= {saveAddressDetails} className="saveBtn">Save</Button>

              </div>
            </div>
          </div>
         }
     

         

          {cart.cartItems.map((item, index) => (
            <div className="checkoutGrid__div">
              <div className="checkoutGrid__div__heading">
                <h3 className="checkoutGrid__div__serialNumb">3</h3>
                <h3 className="checkout__div__heading__title">ORDER SUMMARY</h3>
              </div>
              <div className="checkout__item__grid">
                <div>
                  <div className="checkout__item__img__container">
                    <div className="checkout__item__img">
                      <img
                        src={item.image[0]}
                        width="100%"
                        height="100%"
                        alt="product image"
                      />
                    </div>
                  </div>
                  <div className="checkout__item__qty">Item {item.qty}</div>
                </div>
                <div>
                  <h3 className="checkout__item__title">{item.title}</h3>
                  <h4 className="checkout__item__price">
                    &#8377;{item.qty * item.price}
                  </h4>
                </div>
                <div>
                  <h3 className="checkout__item__delivery">
                    Apply for Delivery{" "}
                  </h3>
                </div>
              </div>
            </div>
          ))}

          <div className="checkout__confirmation__email">
            <h3>
              Order Confirmation Email Sent to <span>{userInfo.email}</span> after checkout.
            </h3>
          </div>

          <div className="checkoutContainer">
            <h3 className="checkoutGrid__serialNumb">4</h3>
            <div className="checkoutLogin">
              <h3 className="checkout__heading__title">PAYMENT METHOD</h3>
              <RadioGroup
                row
                aria-label="position"
                name="position"
                defaultValue="top"
                className="d-flex flex-column p-2"
              >
                <div className="checkoutpage__paymentMethod__radioBtn">
                <input
                  type="radio"
                  name="paymentMode"
                  id="Credit div or paypal"
                  onChange={(e) => setPaymentMethod(e.target.value)}
                  onClick={displayRazorpay}
                  value="Credit card or Netbanking"
                />
                  <label  for="Credit div or paypal">Credit card or Netbanking</label><br/>
                </div>
                <div className="checkoutpage__paymentMethod__radioBtn">
                <input
                type="radio"
                  name="paymentMode"
                  id="Cash On Delivery"
                  onClick={( ) => setShowCheckout(false)}
                  onChange={(e) => setPaymentMethod(e.target.value)}
                  value="Cash On Delivery"
                />
                 <label  for="Cash On Delivery">Cash On Delivery</label><br/>
                </div>
              </RadioGroup>
            </div>
            {!showCheckout ? <Button className="checkout__btn" onClick={placeOrderHandler}>Checkout</Button> : null}
          </div>
        </div>

        <div className="checkoutGrid__right">
          <div className="checkoutSticky__block">
          <div className="checkout__priceDetail">
            <h3>PRICE DETAILS</h3>
            <hr />
            <div className="checkout__priceDetail__flex">
              <h3>Price </h3>
              <h3> &#8377;{cart.totalPrice}</h3>
            </div>
            <div className="checkout__priceDetail__flex deliveryCharges">
              <h3>Delivery charges</h3>
              <h3> &#8377;50</h3>
            </div>
            <div className="checkout__priceDetail__flex">
              <h3>Amount Payable</h3>
              <h3> &#8377;{cart.totalPrice}</h3>
            </div>
          </div>
          <div className="secure__payment">
             <SecurityIcon/>
            <h3>safe and secure payment , 100% authentic products.</h3>
          </div>
        </div>
        </div>

      </div>

    
    </>
  );
};

export default withRouter(PlaceOrder);
