import {PRODUCT_LIST_REQUEST , PRODUCT_LIST_SUCCESS , PRODUCT_LIST_FAILS ,PRODUCT_DETAILS_REQUEST,
    PRODUCT_DETAILS_SUCCESS} from "../../constants/productConstant"
import axios from "axios";

export const listProducts = () => async (dispatch) => {
  try {
      dispatch({type:PRODUCT_LIST_REQUEST});
       const {data} = await axios.get('/api/products');
       dispatch({
           type:PRODUCT_LIST_SUCCESS,
           payload:data
       })
  } catch (error) {
      dispatch({
          type:PRODUCT_LIST_FAILS,
          payload:error.response && error.response.data.message 
          ? error.response.data.message 
          : error.message,
      })
  }
};


export const listProductDetails = (number) => async(dispatch) => {
             try {
                 dispatch({ type:PRODUCT_DETAILS_REQUEST })
                 const {data} = await axios.get(`/api/products/${number}`)
                 dispatch({type:PRODUCT_DETAILS_SUCCESS, payload:data})
             } catch (error) {
                 dispatch({type:PRODUCT_LIST_FAILS , payload:error.response && error.response.data.message 
                    ? error.response.data.message 
                    : error.message, })
             }
}