import React, { useEffect, useState } from "react";
import { Link, withRouter } from "react-router-dom";
import "../App.css";
import { login } from "../actions/userAction";
import { Register } from "../actions/userAction";

import { useDispatch, useSelector } from "react-redux";
import { Button, Card, CircularProgress } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import validator from "validator";
import CancelIcon from '@mui/icons-material/Cancel';



const Signin = ({ location, history }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState();

  const redirect = location.search
    ? location.search.split("=")[1]
    : "/store";
  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { loading, error, userInfo } = userLogin;
  

  const [newUser , setnewUser ] = useState(true);


  useEffect(() => {
    if (userInfo) {
      history.push(redirect);
    }
  }, [history, userInfo, redirect]);

  const loginSubmitHandler = (e) => {
    e.preventDefault();
    if (email && password) {
      dispatch(login(email, password));
    } else {
      setMessage("Fill all the field");
    }
  };

  
  const [name, setName] = useState("");
  // const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState();
  // const [password, setPassword] = useState("");
  const [cpassword, setCPassword] = useState("");
  // const [message, setMessage] = useState("");

  // const redirect = location.search ? location.search.split("=")[1] : "/";
  // // const dispatch = useDispatch();
  const userRegister = useSelector((state) => state.userRegister);
  const { regloading, regerror } = userRegister;
  const [loginStatus , setLoginStatus ] = useState(false);

  

  const registerSubmitHandler = (e) => {
    e.preventDefault();
    //dispatch
    if (name && email && mobile && password) {
      if (name && !validator.matches(name, "^[a-zA-Z]{2,40} [a-zA-Z]{2,40}$")) {
        setMessage("Name is not valid");
      } else if (email && !validator.isEmail(email)) {
        setMessage("Email Not Valid");
      } else if (mobile && !validator.isMobilePhone(mobile, "en-IN")) {
        setMessage("Mobile Number is Invalid");
      } else if (password !== cpassword) {
        setMessage("Password do not match");
      } else {
        console.log("check dispatch")
        dispatch(Register(name, email, mobile, password));
        console.log("register successfully")
      }
    } else {
      setMessage("Fill all the fields");
    }
  };



  return (<>
      <div className="d-flex "> {
      newUser ? 
      <form
      onSubmit={loginSubmitHandler}
      className="col-lg-12 col-sm row  p-4  loginForm"
    >
        <div className="col-lg-4 login__tagline ">
          <h3>Local Roots, Global Reach</h3>
          <img src="/pictures/apneehatti_logo.svg"/>
        </div>
        <div className="signin col-lg-8 d-flex justify-content-center align-items-center">
          <div className="formcontainer">
          <div align="center">
      {error && (
        <Alert severity="error" variant="filled">
          {error}
        </Alert>
      )}
      {loading && <CircularProgress />}
      {message && (
        <Alert severity="error" variant="filled">
          {message}
        </Alert>
      )}
    </div>
            <div className="formLoginSection">
              <h6 className="fw-bold mb-4">Login to your account</h6>
              <div className="inputField">
                <input
                  className="form_input"
                  type="email"
                  id="semail"
                  name="semail"
                  placeholder="Your Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>

              <div className="inputField">
                <div className="forgotPasswordContainer text-end">
                  <a
                    className="forgotPassword mb-1 "
                    href="/forgotpassword"
                  >
                    <strong>Recover Password</strong>
                  </a>
                </div>
                <input
                  className="form_input"
                  type="password"
                  id="spassword"
                  name="spassword"
                  value={password}
                  placeholder="Password"
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>

              <Button
                variant="contained"
                type="submit"
                fullWidth
              >
                Log In
              </Button>

              <p className="mt-2">
                New To Apneehatti ? &nbsp; <span onClick={()=>setnewUser(false)}>Sign Up</span>
              </p>
            </div>
          </div>
        </div>

    
    </form>
    :
    <div className="col-sm col-lg-12 row p-4 loginForm  ">
    <div className="col-lg-4 login__tagline ">
            <h3>Local Roots , Global Reach</h3>
            <img src="/pictures/apneehatti_logo.svg"/>
          </div>            
    <div className="signin col-lg-8 d-flex justify-content-center align-items-center flex-column"  >
     <div className="formSignUpContainer col-md-12 col-sm justify-content-center" >
     
        <div align="center">
      {regerror && (
        <Alert severity="error" variant="filled">
          {regerror}
        </Alert>
      )}
      {regloading && <CircularProgress />}
      {message && (
        <Alert severity="error" variant="filled">
          {message}
        </Alert>
      )}
    </div>
    
<h6 className="fw-bold mb-4 text-uppercase">
          Register your account
        </h6>
        <form
          autoComplete="off"
          onSubmit={registerSubmitHandler}
          className="formLoginSection"
        
        >
          <div   className="formsection">
          <div className="inputField">
            <label className="form__label">
              <span>Full Name</span>
            </label>
            <input
              className="form_input"
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </div>

          <div className="inputField">
            <label className="form__label">
              <span>Email</span>
            </label>
            <input
              className="form_input"
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>

          <div className="inputField">
            <label className="form__label">
              <span>Mobile Number</span>
            </label>
            <input
              className="form_input"
              type="text"
              maxLength="10"
              value={mobile}
              onChange={(e) => setMobile(e.target.value)}
            />
          </div>

          <div className="inputField">
            <label className="form__label">
              <span>Password</span>
            </label>
            <input
              className="form_input"
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>

          <div className="inputField">
            <label className="form__label">
              <span>Confirm Password</span>
            </label>
            <input
              className="form_input"
              type="password"
              value={cpassword}
              onChange={(e) => setCPassword(e.target.value)}
            />
          </div>

          </div>
          <Button
            variant="contained"
            fullWidth
            color="primary"
            type="submit"
            className="formbtn"
          >
            Register
          </Button>
          <p>
            Have An Account ! &nbsp;<span onClick={() => setnewUser(true)}>Sign In</span>
          </p>
         
        </form>
      
      </div>
    </div>
  </div>

    }   
     {/* <div className="cancelIcon"><CancelIcon onClick={() => setLoginStatus(false)}/></div> */}
     
  </div> 
  </>);
};

export default withRouter(Signin);
