import React, { useEffect, useState } from "react";
import { getUserDetails, updateUserProfile } from "../../actions/userAction";
import { useDispatch, useSelector } from "react-redux";
import {
  Avatar,
    Button,
  CircularProgress,
  Container,
  Icon,
  Table,
} from "@material-ui/core";
import { ListMyOrders } from "../actions/orderAction";
import dateFormat from "dateformat";
import Alert from "@material-ui/lab/Alert";
import validator from "validator"


const UserProfileInfo = () => {
    const [message, setMessage] = useState("");
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [mobile, setMobile] = useState("");
    const [password, setPassword] = useState("");
    const [inputDisable , setInputDisable] = useState(true)
  
    const dispatch = useDispatch();
    const userDetails = useSelector((state) => state.userDetails);
    const userLogin = useSelector((state) => state.userLogin);
    const userUpdateProfile = useSelector((state) => state.userUpdateProfile);
    const { success } = userUpdateProfile;
  
  
    const { loading, error, user } = userDetails;
    const { userInfo } = userLogin;
  

    useEffect(() => {
            if (!user.name) {
              dispatch(getUserDetails("profile"));
            } else {
              setName(user.name);
              setEmail(user.email);
              setMobile(user.mobile);
            }
    }, [ user, dispatch])

    const submitHandler = (e) => {
        e.preventDefault();
        //dispatch
         console.log(mobile)

        if(validator.isMobilePhone(mobile ,['en-IN'])) {
          dispatch(updateUserProfile({ id: user._id, name, email, mobile, password }));
        }else{
          setMessage("Invalid Mobile Number")
        }
      };
      
    return (
    <div>
        <div>
    {message && <Alert severity="error" variant="filled">{message}</Alert> }
       { error && (
          <Alert variant="filled" severity="error">
            {error}
          </Alert>
        ) }
       { success && (
          <Alert variant="filled" severity="success">
            Profile Updated
          </Alert>
        )  
       }

       
        {loading && <CircularProgress />}
      </div>
        <Container className="userProfileContainer border-1 row">
            
        <h4>Your Profile</h4>
          <div className="yourProfileContainer p-0 ">
            <div className="yourProfile">
              <div className="yourProfileDetail">
                <Avatar />
                <h5>
                  {name} <br />
                  <span>
                    {email} <br />
                    Mobile No : {mobile}
                  </span>
                </h5>
              </div>
             
            </div>
          </div>
          <Container className="yourProfileUpdateContainer glassMorphism mt-2">
            <h6 className="text-uppercase mb-3 ">Update Your Profile</h6>
            <form
              noValidate
              autoComplete="off"
              className="updateFormsection"
            >
              <div className="inputField">
                <label className="form__label__UserProfile">
                 Full Name
                </label>
                <input
                  disabled = {inputDisable}
                  className="form_input"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </div>

              <div className="inputField">
                <label className="form__label__UserProfile">
                 Email
                </label>
                <input
                  disabled = {inputDisable}
                  className="form_input"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>

              <div className="inputField">
                <label className="form__label__UserProfile">
                 Mobile Number
                </label>
                <input
                  disabled = {inputDisable}
                  className="form_input"
                  type="text"
                  value={mobile}
                  onChange={(e) => setMobile(e.target.value)}
                />
              </div>

              <div className="inputField">
                <label className="form__label__UserProfile">Password</label>
                <input
                  disabled = {inputDisable}
                  className="form_input"
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>

              {inputDisable ? 
                <Button
                type="submit"
                variant="contained"
                color="primary"
                className="formbtn"
                onClick={()=> setInputDisable(false)}
              >
                Edit
              </Button>
              :
              <Button
                variant="contained"
                color="primary"
                className="formbtn"
                onClick={submitHandler}
              >
                Update
              </Button>}
            </form>
          </Container>
      </Container>
      </div>
  )
}

export default UserProfileInfo
