import React, { useEffect } from "react";
import {useSelector , useDispatch} from "react-redux"
import { Link, Redirect, Route, withRouter } from "react-router-dom";
import {
  Avatar,
  CircularProgress,
  Container,
} from "@material-ui/core";
import PersonIcon from '@mui/icons-material/Person';
import { logout } from "../../actions/userAction";

import LocalMallIcon from '@mui/icons-material/LocalMall';
import PowerSettingsNewIcon from '@mui/icons-material/PowerSettingsNew';
import UserOrder from "./UserOrder";
import UserProfileInfo from "./UserProfileInfo";
import Header from "../../components/Header";

const UserProfile = ({ location, history }) => {
  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if (!userInfo) {
      history.push("/");
    }
  }, [history, userInfo]);

  const logoutHandler = () => {
    dispatch(logout());
    history.push("/");
  };

  return (<>
  <Header/>
      <div className="userProfile">
        <div className="Profile">
          <div className="reg">
         

            <div className="userProfile__container" >
              <div className="col-lg-3 userProfile__sidebar mt-2 ">
               <div className="userProfile__sidebar_userInfo glassMorphism">
                  <li className="userProfile__sidebar__hello">Hello,</li>
                  <li className="userProfile__sidebar__name">{userInfo.name}</li>
               </div>
               <div className="mt-3 userProfile__sidebar__sidebar glassMorphism">
               <Link to="/user/userprofile"><li className="userProfile__sidebar__item"><PersonIcon className="me-3"/> my profile</li></Link>
                <Link to="/user/myorders"><li className="userProfile__sidebar__item"><LocalMallIcon className="me-3"/> my orders</li></Link>
                 <li className="userProfile__sidebar__item"onClick={logoutHandler}><PowerSettingsNewIcon className="me-3" /> logout</li>
               </div>
              </div>
              <div className="col-lg-9">
                <Route path="/user" ><Redirect to ="/user/userprofile"/></Route>
                <Route path="/user/myorders" component={UserOrder}/>
                <Route path="/user/userprofile" component={UserProfileInfo}/>

              </div>
            </div>
          </div>
        </div>

       </div>
  </>);
};

export default withRouter(UserProfile);
