import React from "react";
import { Link, useLocation, withRouter } from "react-router-dom";

function Product({ proinfo, match }) {
  const location = useLocation();
  const store = location.pathname.split("/").slice(1)[0];
  return (
    <div className="product  ">
      <div className="product__img__container">
      <Link to={`/store/${proinfo.category}/products/${proinfo._id}`}>
        <img
          align="center"
          src={proinfo.image[0]}
          width="100%"
          height="100%"
        />
      </Link>
      </div>
      <div className="product_footer " align="left">
        <div className="lt_side">
        <h7 className="category ">{proinfo.category}</h7>
          <div className="product_footer_footer ">
          <h6 className="title ">{proinfo.title}</h6>
          <h7 className="price"> &#x20B9; {proinfo.price}</h7>
          </div>
        </div>
      </div>
    </div>
  );
}

export default withRouter(Product);
