import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { listOrder } from "../actions/sellerAction";
import { deliverdOrder } from "../actions/orderAction";
import { createDeliveryOrder } from "../actions/deliveryAction";

import BorderColorOutlinedIcon from "@material-ui/icons/BorderColorOutlined";
import { withRouter } from "react-router";
import { Button } from "@material-ui/core";
import CircularProgress from "@mui/material/CircularProgress";

const SellerOrderList = ({ history }) => {
  const [update, setupdate] = useState(false);


  const showUpdate = () => {
    setupdate(!update);
  };

  // sellerInfo
  const sellerLog = useSelector((state) => state.sellerLog);
  const { sellerInfo } = sellerLog;
  console.log(sellerInfo);
  const dispatch = useDispatch();
  const orderList = useSelector((state) => state.orderList);
  const { loading:orderListLoading, error, orderReq } = orderList;

  console.log();
  // console.log(store[0])
  // console.log(store[0] == sellerInfo.store ? "volla both are same" : "bad luck");

  console.log(
    orderReq.map((ord) =>
      ord.orderItems.filter((sub) => sub.store === sellerInfo.store) ===
      "shumbhati"
        ? "vola"
        : "null"
    )
  );
  // DELIVERY API INTEGRATE
  const deliveryOrderCreate = useSelector((state) => state.orderCreate)
  const {deliveryOrdCreate, success , loading: deliveryLoading} = deliveryOrderCreate;

  const orderDeliverd = useSelector((state) => state.orderDeliverd);
  const { loading: loadingDelivered, } =
    orderDeliverd;
  

  const productTitle = orderReq.map((ord) =>
    ord.orderItems
      .filter((sub) => sub.store === sellerInfo.store)
      .map((sub) => sub.title)
  );
  const proTitle = productTitle.map((i) => i.length);
  useEffect(() => {
    if (error) {
      history.push("/sellerdashboard");
    } else {
      dispatch(listOrder());
    }
  }, [listOrder]);
  
  
   

  const addDeliveryStatus = (number , productNumber) => {
    const name = orderReq.filter((i) => i._id == number).map(i => i)
    console.log(name)

    alert("Order status successfully change to deliverd");
    
    const fullName = orderReq.filter((i) => i._id == number).map(i => i.shippingAddress.fullName);
    const address = orderReq.filter((i) => i._id == number).map(i => i.shippingAddress.address);
    const city = orderReq.filter((i) => i._id == number).map(i => i.shippingAddress.city);
    const postalcode = orderReq.filter((i) => i._id == number).map(i => i.shippingAddress.postalcode);
    const shippingMobile = orderReq.filter((i) => i._id == number).map(i => i.shippingAddress.shippingMobile);
    const productNameObj = orderReq.filter((i) => i._id == number).map(i => i.orderItems.filter((x) => x._id == productNumber).map(x => x.title))
    const productName = productNameObj[0];
    const qtyObj = orderReq.filter((i) => i._id == number).map(i => i.orderItems.filter((x) => x._id == productNumber).map(x => x.qty))
    const qty = qtyObj[0];
    const productidObj = orderReq.filter((i) => i._id == number).map(i => i.orderItems.filter((x) => x._id == productNumber).map(x => x._id))
    const productid = productidObj[0];  
    const priceObj = orderReq.filter((i) => i._id == number).map(i => i.orderItems.filter((x) => x._id == productNumber).map(x => x.price))
    const price = priceObj[0]
    const paymentMethod = orderReq.filter((i) => i._id == number).map(i => i.paymentMethod == "Cash On Delivery" ? "COD" : "Prepaid")


  
    dispatch(createDeliveryOrder({
      order_id: productNumber,
      order_date: "2021-10-28T08:54:02.779Z",
      pickup_location: "kannad",
      billing_customer_name:fullName[0] ,
      billing_last_name:" ",
      billing_address:address[0],
      billing_address_2:" ",
      billing_city:city[0] ,
      billing_pincode:postalcode[0] ,
      billing_state: "Maharashtra",
      billing_country: "india",
      billing_email: "",
      billing_phone:shippingMobile[0] ,
      shipping_is_billing: 1,
      shipping_customer_name:" ",
      shipping_last_name:" ",
      shipping_address:" ",
      shipping_address_2:" ",
      shipping_city:" ",
      shipping_pincode:" ",
      shipping_country:" ",
      shipping_state:" ",
      shipping_email:" ",
      shipping_phone:" ",
      order_items: [
        {
          name:productName[0],
          sku:productid[0] ,
          units:qty[0] ,
          selling_price:price[0] ,
    
        }
      ],
      payment_method:paymentMethod[0],
      sub_total:price[0] ,
      length: "10",
      breadth: "10",
      height: "10",
      weight: "1.5",
      })
      );
      dispatch(deliverdOrder(number, productNumber))

      !deliveryLoading ? window.location.href = "/sellerdashboard" : null;

  };

  return (
    <div className="sellerOrderList">
      <table className="table  table-hover table-light table-bordered ">
        <thead className="table-dark">
          <tr>
            <th scope="col">#</th>
            <th scope="col">Product</th>
            <th scope="col">Order Id</th>
            <th scope="col">Quantity</th>
            <th scope="col">Status</th>
            <th scope="col">Detail</th>
          </tr>
        </thead>
        {!orderListLoading ? (
          <tbody>
            {orderReq
              .filter(
                (ord) =>
                  ord.orderItems.filter((sub) => sub.store === sellerInfo.store)
                    .length > 0
              )
              .map((ord, index) => (
                <tr key={ord._id}>
                  <td>{index + 1}</td>
                  <td style={{ fontSize: "14px" }}>
                    {ord.orderItems
                      .filter((sub) => sub.store === sellerInfo.store)
                      .map((sub) => (
                        <li>{sub.title}</li>
                      ))}
                  </td>
                  <td style={{ fontSize: "14px" }}>
                    <li>#{ord._id}</li>
                  </td>

                  <td style={{ fontSize: "14px" }}>
                    {ord.orderItems
                      .filter((sub) => sub.store === sellerInfo.store)
                      .map((sub) => (
                        <li>{sub.qty}</li>
                      ))}
                  </td>
                  <td style={{ fontSize: "14px" }} className="orderStatusTable" >
                  {ord.orderItems.filter((sub) => sub.store === sellerInfo.store).map((sub , subIndex) =>sub.isDelevered ?<li>Order is delivered</li>  :  <Button onClick={(e) => addDeliveryStatus(ord._id , sub._id )}>Deliver</Button>)}
                    </td>
                  



                  <td style={{ fontSize: "14px" }}>
                    <Button>view detail</Button>
                  </td>
                </tr>
              ))}
          </tbody>
        ) : (
          <CircularProgress />
        )}
      </table>
    </div>
  );
};

export default withRouter(SellerOrderList);
