import React, {useEffect , useState} from "react";
import SellerOrderList from "./SellerOrderList";
import {useDispatch, useSelector} from "react-redux"
import PersonIcon from '@material-ui/icons/Person';
import ExitToAppOutlinedIcon from '@material-ui/icons/ExitToAppOutlined';
import { logout } from "../actions/sellerAction";



const SellerLayout = ({history , location}) => {
  const sellerLog = useSelector((state) => state.sellerLog);
  const { sellerInfo } = sellerLog;
  const dispatch = useDispatch();
  const [comp, setComp] = useState(false)
 
  const redirect = location.search ? location.search.split("=")[1] : "./seller";


  const logoutHandler = () => {
    dispatch(logout());
    history.push(redirect)
  };

  
  useEffect(() => {
    if(!sellerInfo){
     history.push('/seller')
    }
   }, [sellerInfo])

  return (
      <div className="">
        <nav className="navbar navbar-light bg-light sellerNav">
          <div className="container-fluid">
            <a className="navbar-brand" href="#">
                Seller Dashboard
            </a>
            <div className="d-flex">
            <a className="navbar-brand fs-7" href="#">
                       <PersonIcon/>
                     <h7>{sellerInfo.name}</h7>
            </a>
            <div className="navbar-brand fs-7 d-flex align-items-center " onClick={logoutHandler}>
                     <ExitToAppOutlinedIcon/>
                     <h7>Logout</h7>
               </div>
            </div>
          
          </div>
        </nav>
        <div className="d-flex">
        <div className="col-2 sellerSidebar pt-5">
           <div className="d-flex justify-content-center align-items-center" >
               Dashboard
           </div>
           <div className="d-flex justify-content-center align-items-center" >
               Listing
           </div>
        </div> <SellerOrderList className="col-10"/></div>
      </div>
  );
};

export default SellerLayout;
