import React, { useEffect, useState } from "react";
import { addToCart, removeFromCart } from "../actions/cartAction";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, withRouter } from "react-router";
import { Button, FormControl, Link, MenuItem, Select } from "@material-ui/core";
import ClearOutlinedIcon from "@material-ui/icons/ClearOutlined";
import IconButton from "@material-ui/core/IconButton";
import CancelIcon from "@mui/icons-material/Cancel";
import Signin from "../../components/Signin";
import CloseIcon from '@mui/icons-material/Close';
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";


const Cart = ({ match, location, history }) => {
  // handling multiple stores by there name in url
  const locationPath = useLocation();
  const store = locationPath.pathname.split("/").slice(1)[0];

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const productId = match.params.number;
  const qty = location.search ? Number(location.search.split("=")[1]) : 1;
  const dispatch = useDispatch();
  const cart = useSelector((state) => state.cart);
  const { cartItems } = cart;
  // console.log(cartItems);

  const [loginStatus, setLoginStatus] = useState(false);

  useEffect(() => {
    if (productId) {
      dispatch(addToCart(productId, qty));
    }
  }, [dispatch, productId, qty]);

  const removeFromCartHandler = (number) => {
    dispatch(removeFromCart(number));
  };
  console.log("localstorage");

  const checkOut = () => {
    if (!userInfo) {
      setLoginStatus(true);
    } else {
      history.push("/shipping/placeorder");
    }
  };

  $(document).ready(function(){

    $("#hide").click(function(){
      $(".cart__div").addClass("cart__div__hide");
    });
   
  });

  const showAndHideCart = () => {
    if (localStorage.getItem("isChecked") != null && localStorage.getItem("isChecked") == "true") {
        localStorage.setItem("isChecked", "false")
          
  $(document).ready(function(){
      $(".cart__div").addClass("cart__div__hide");
  });
    }
    else {
        localStorage.setItem("isChecked", "true");
        $(document).ready(function(){
          $(".cart__div").removeClass("cart__div__hide");
      });
    }}
  return (
    <div className="cart__div cart__div__hide">
     
      {!userInfo ? (
        loginStatus ? (
          <div className="loginBlockOuter">
            <div className="loginBlockMain">
              <div className="loginBlock">
                <Signin />
                <div className="cancelIcon">
                  <CancelIcon onClick={() => setLoginStatus(false)} />
                </div>
              </div>
            </div>
          </div>
        ) : null
      ) : null}
      <div className="cartpage ">
        
        <div className="shoppingCart">
        <div className="cart__closeIcon" id="hide" onClick={showAndHideCart}>
          <Button>
          <CloseIcon/>
          </Button>
        </div>

          <div className="">
            {cartItems.length === 0 ? (
              <div className="emptyCart">
                <div className="emptyCart__title">
                  <h2>Apneehatti</h2>
                </div>
                <ShoppingCartIcon/>
                <h3>
                  Your Cart Is Empty <br /> Fill Your Basket <br />
                  <a onClick={showAndHideCart}>Go Back</a>
                </h3>
              </div>
            ) : (
              <div className="cart__flex">
                <div className=" ">
                  {cartItems.map((item , index) => (
                    <div className="cart__items  " key={index}>
                      <div className="productCart__imgContainer">
                      <img 
                        src={item.image[0]}
                        alt=""
                      
                      />
                      </div>
                      <div className="cart__item__title d-flex flex-column">
                      <Link
                        to={`/product/${item.product}`}
                        className=""
                      >
                        {item.title}
                      </Link>
                      <FormControl variant="outlined">
                        <Select
                          labelId="Quantity"
                          id="demo-simple-select-outlined"
                          value={item.qty}
                          onChange={(e) =>
                            dispatch(
                              addToCart(item.product, Number(e.target.value))
                            )
                          }
                        >
                          {[...Array(item.countInStock).keys()].map((x) => (
                            <MenuItem key={x + 1} value={x + 1}>
                              {x + 1}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                      </div>
                      <h3 className="cart__item__price"> &#8377; {item.qty * item.price}</h3>
                      <Button
                        onClick={() => removeFromCartHandler(item.product)}
                      >
                        <ClearOutlinedIcon />
                      </Button>
                    </div>
                  ))}
                </div>
                
                 <div >
                 <hr/>

                 <div className="d-flex justify-content-between cart__subtotal__container">
                      <h3 className="cart__subtotal__text">Subtotal</h3>
                      <h3 className="cart__subtotal__price">&#8377;
                        {cartItems
                          .reduce((acc, item) => acc + item.qty * item.price, 0)
                          .toFixed(2)}
                      </h3>
                    </div>
                    <div className="cart__subtotal__btn__container">
                    <Button
                    className="cart__subtotal__btn"
                    onClick={() => checkOut()}
                    disabled={cartItems.length === 0}
                    fullWidth
                  >
                    Proceed to Checkout
                  </Button>
                  <p>Need help ? Contact us at 1818181818</p>
                    </div>
                 </div>
                </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Cart);
